      .col-lg-6(data-aos="fade-left")
        p Cuando se habla de macroentorno de la empresa se hace referencia a todos los aspectos que están alrededor y que pueden afectar, a pesar de no encontrarse exclusivamente en su sector. Se contrapone con el microentorno, principalmente porque este último tiene un enfoque más concreto.
        p Una de las áreas ligada al macroentorno y que afecta de manera directa a la empresa tiene que ver con la economía, es decir, si la economía en general se resiente, la empresa se afectará generando con esto dificultades en sus utilidades. Además, no se debe olvidar que la economía es un factor cíclico, es decir, hay tiempos en que el PIB registra un buen nivel, como otros en que no.
        .cajon.color-secundario.fondo1.p-4
          p.mb-0 Entonces el análisis del macroentorno se refiere al estudio de las variables que influyen en la actividad empresarial.  Variables que por lo general se relacionan no solo con la empresa, sino también a nivel social y las actividades que dependen de esta e incluyen aspectos relacionados con la población, las políticas públicas o las normativas tecnológicas. Un buen análisis del macroentorno permite realizar que la empresa se prepare y proteja para épocas menos fructíferas a nivel de ingresos.
    
    p.mb-4(data-aos="fade-right") Uno de los métodos utilizado como herramienta para el análisis del macroentorno es el denominado PESTEL, el cual se explica a continuación.
    
    separador
    #t_2_1.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 2.1 Método PESTEL
    
    .row.justify-content-center.ampliarambos.fondo10.px-5.mb-5
