        p(data-aos="fade-right") Tras realizar los análisis externo e interno ya se está en condiciones de desarrollar el análisis DOFA. Esta es una matriz que permite relacionar las amenazas y oportunidades (análisis externo) con las fortalezas y debilidades (análisis interno). 
