    #t_2_3.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 2.3 Análisis del negocio - Modelo Canvas
    
    p.mb-5(data-aos="fade-right") Hasta este momento se han comentado algunas herramientas de análisis estratégico. Además de estas herramientas existe un modelo que por su simplicidad, eficacia y practicidad ha ganado bastantes adeptos, este modelo de análisis contempla todos los factores que impactan el desempeño de una empresa de una forma ágil e intuitiva. Se trata de un método que aligera la tarea de análisis y elimina todo lo superfluo para centrarse en el modelo de negocio sin perder el enfoque estratégico. Ver figura.
    
    .row.justify-content-center.mb-5
