        p Y luego lanza un cuestionamiento sencillo, pero que tiene una gran profundidad. El autor pregunta:
        .cajon.puntacajon.bg3.sombracajon.p-4.mb-4
          p.mb-0 “¿Cómo te imaginas el modelo de negocio de tu empresa dentro de dos, cinco o diez años? ¿Te encontrarás entre los principales jugadores? ¿Podrás hacer frente a los competidores que tengan entre sus manos fantásticos y nuevos modelos de negocio?”
        p.mb-0 La verdad es que muchos planes de empresa fracasan por invertir recursos (tiempo y dinero) construyendo el producto equivocado, y es ahí donde el Modelo Canvas se convierte en una herramienta estratégica empresarial y de gestión que permite describir, diseñar y testear nuevos modelos de negocio. Permite hacer un mapa completo del modelo de negocio en una sola imagen, partiendo de un lienzo predefinido en nueve casillas que representan los fundamentos en los que se asienta el negocio. 
    
    p.mb-5(data-aos="fade-right") Las nueve casillas que se indican anteriormente podrán ser consultadas en el siguiente recurso educativo:
    
